import React, { useState } from 'react';
import './retroButton.css'
import styled from 'styled-components'


function RetroButton  (props) {

         let level = props.text
     
        return ( 
            
            <DivButton >
               <Buttons 
                    className={level} 
                    type="button" 
                    value={level} 
                     /> 
           
        </DivButton>
        );
        
}
 
export default RetroButton;


const DivButton = styled.div`

.Easy {

    background-color: #908fbb;
    box-shadow: -3px 5px #66658d;
}
.Easy:active {

    color: #908fbb;
    background-color: #66658d;
    box-shadow: 0 2px  #908fbb;
    left: 50%;
}

.Medium {

    background-color: #4ea6a6;
    box-shadow: -3px 5px #206f6f;
}
.Medium: active {

    color: #4ea6a6;
    background-color: #206f6f;
    box-shadow: 0 2px  #4ea6a6;
    left: 50%;
}
.Hard {
    background-color: #da6964;
    box-shadow: -3px 5px #98342f;
}
.Hard:active {
    color: #da6964;
    background-color: #98342f;
    box-shadow: 0 2px #da6964;
    left: 50%;
}
.Free{
    background-color: #50a64e;
    box-shadow: -3px 5px #206f33;
  
}
.Free:active {
    color: #da6964;
    background-color: #98342f;
    box-shadow: 0 2px #da6964;
  
  
}
.Paid {
    background-color: #0097d3;
    box-shadow: -3px 5px #002e83;
  
    
    
}
.Paid:active {
    color: #da6964;
    background-color: #98342f;
    box-shadow: 0 2px #da6964;
   
}
.All {
    background-color: #ffc500;
    box-shadow: -3px 5px #c6571b;
    margin: 0px 0 0 0 ;
    padding: 0px 5px 0 5px;
    height: 32px;
    font-size: 20px;

}
.All:active {
    color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
 
    
}

  width: auto;
  height: 32px;
  padding: 10px 0 10px 0;
  //border: 1px solid #c3c3c3;
  
  display: block;
  
  align-content: center;
  position: relative;

`;
const Buttons = styled.input`
outline: none;
background-color: #ffc500;
color:#eaeaea;
border-radius: 5px;
font-size: large;
font-family: "Press Start 2P";
letter-spacing: 1.5px;
text-shadow: 2px -1px #5c553e;
padding: 10px;
box-shadow: -3px 5px #c6571b;
border: none;
display: block;
margin: 0 0 0 0;
position: absolute;
top: 50%;
left: 51%;
-ms-transform: translate(-50%, -50%);
transform: translate(-50%, -50%);

`; 