import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
//icons
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PlayIcon from '../../assets/icons/play.png';
import { PowerInputSharp } from '@material-ui/icons';


const Products = (props) => {
    
        return ( 
            
            <Product id={props.id}>

               
                <Imagen>   
                    
                    <Level className={props.level}> {props.level} </Level> <img src={props.img}/> 
                    <Contenido>
                    <h1 className="Titulo">{props.title}</h1>
                    <p className="Descripcion">{props.description}</p>

                </Contenido>
                
                
                </Imagen>
            

                

                <div className="div">
          
                    <Time> <p className="Time"><b>Time: </b> {props.time} min.</p></Time>
                    <div className="sale"><h1>SALE</h1></div>
                
                    <p>{props.discount}% Disc. <b className="b">${props.price} USD</b></p>

                    <PriceButton className="piceButton">

                        <div className="icon"><ShoppingCartIcon className="ICON" /></div>

                        <div className="price" ><h1> $ {(props.price - (props.price * props.discount/100)).toFixed(2)}</h1> <h1 className="usd">USD</h1> </div>

                    </PriceButton>

                </div>
               
            </Product>

         );
    
}
 
export default Products;

const Product = styled.div`
width:100%
heigth: 359px;
display: inline-block;

.easy{
    background-color: #908fbb;
    font-size:10px;
}
.medium{
    background-color: #4ea6a6;
    font-size:10px;  
}
.hard{
    background-color: #da6964;
}


.piceButton{
    color:#fff;
    background-color:#00b2bd;
}


@media (min-width: 768px) {  
    width: 45%;
}
@media (min-width: 968px) {  
    width: 30%;
}
`;
const Level = styled.div`
width:72.4px;
height:27.4px;


margin: 15px 0 -100% 20px ;

z-index: 2;
position:relative;

line-height:25.99px;
font-family: "Press Start 2P";
float:left;
text-align: center;
color: #fff;
text-shadow: 1.5px 1px #000;
font-size:11px;
border-radius: 2px;
background-color:#f6ca00;

`;
const Imagen = styled.div`
width:90%;
heigth:200.8px;
overflow: hidden;
margin: auto;
img{
    width:100%;
    heigth:100%;
    border-radius: 10px;
}
`;
const Contenido = styled.div`
width:90%;
margin: auto;
margin-top:-60px;
margin-bottom: 30px;
padding: 10px 0px 0px 0px;
height: 106px;
z-index: 2;
position:relative;

 background-color:#fff;
 box-shadow: 0px 10px 15px 0.20px #262626;
 border-radius: 10px;

`;


const Time = styled.div `
.Time{
    color:#000;
    margin:0 0 3px 0;
}
`;

const PriceButton = styled.button`
outline: none;
height:28px;
width:191.6px;
border:none;
margin-top:4px;
padding: 2px 0 0 0;
div{
    display:inline-block;
    margin:;
    margin-left:33px ;
    height:100%;
}
.icon{
    float:left;
    
    height:20px;
    width:auto;
    .ICON{
        height:20px;
        margin-top:2px ;
    }
}
.price{
    height:100%;
    width:auto;
    margin-left:3px ;
    display:inline-block;
    float:left;
    letter-spacing: 1px;
    padding:0px 0px 0px 0px;
    font-family: 'Montserrat', sans-serif;
    h1{
        font-size:17px;
        color:#fff;   
        display:inline-block;
    }
    .usd{
        letter-spacing: 0px;
    }
}
`;
