import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import {authService} from '../../DataBase'

const onLogOutClick = () => authService.signOut();
const ButtonLogout = () => {
return (
    <Div>

    <Link to='/login' style={{ textDecoration: 'none' }}> 

       <Button onClick={onLogOutClick} className="ButtonLogout" type="button" value="Log Out"/> 

   </Link>

    </Div>
)
}
const Div = styled.div`
.ButtonLogout:active{
    color: #66658d;
    background-color: #262038;
    box-shadow: 0 2px #66658d;
    transform: translateY(4px);
    transition-duration: 0.03s;
}
`;

const Button = styled.input`
outline: none;
background-color: #66658d;
color: #eaeaea;
/*Size*/
border-radius: 5px;
font-size: large;
font-family: "Press Start 2P";
letter-spacing: 1.5px;
text-shadow: 2px -1px #5c553e;
padding: 10px;
box-shadow: -3px 5px #262038;
border: none;
cursor: pointer;
/*Margin*/
margin-top: 0px;
margin-bottom: 0px;
margin-left: auto;
margin-right: auto;

`;

export default ButtonLogout