import React, { useEffect, useState } from 'react';
import RetroButton from '../retroButton/RetroButton'
import styled from 'styled-components'
import Products from '../../../../components/Products/Porducts'
import {dbService} from '../../../../DataBase'

const Filter = () => {
    
    const [products,setProducts]=useState([])
    const [easy,setEasy] = useState(false)
    const [medium,setMedium] = useState(false)
    const [hard,setHard] = useState(false)
    const [mostrador,setMostrador] = useState('All')
    const fetchProducts = async () => {  
        const dbProduct = await dbService.collection('product').get();
        dbProduct.forEach((document) => {
            const productObject = {
                ...document.data(),

            }

            setProducts((prev) => [productObject, ...prev]);

        });
           
    }

    function onClick(props) {
        let level = props.target.defaultValue

        if (level === "All"){
            setEasy(false) 
            setMedium(false)
            setHard(false)
            setMostrador('All')
        }
        if(level==="Easy"){
            setEasy(current => !current) 
            setMedium(false)
            setHard(false)
            setMostrador('Easy')
            
        }
        if(level==="Medium"){
            setMedium(current => !current) 
            setEasy(false)
            setHard(false)
            setMostrador('Medium')
        } 
        if(level==="Hard"){
            setHard(current => !current) 
            setEasy(false)
            setMedium(false)
            setMostrador('Hard')
        }

        
    
    }

 
 
    const buttons = {

        easy:   <Button onClick={onClick}> <RetroButton text="Easy"  /> </Button> ,
        medium: <Button onClick={onClick}> <RetroButton text="Medium"/> </Button> ,
        hard:   <Button onClick={onClick}> <RetroButton text="Hard"  /> </Button> ,
        all:    <All    onClick={onClick}> <RetroButton text="All"   /> </All>    ,
    }

        useEffect(() => {
            fetchProducts();
          }, [])

          function renderProducts(dish){
              let product = <Products 
              id={dish.id} 
              title={dish.title} 
              description={dish.description}
              img={dish.img} 
              level={dish.level} 
              time={dish.time}
              price={dish.price}
              discount={dish.discount}/>

            if (!easy && !medium && !hard) {
                return product
            } else {
                if(easy && !medium && !hard){
                    if(dish.level==="easy"){
                        return product
                    }
                } else {
                    if(!easy && medium && !hard){
                        if(dish.level==="medium"){
                            return product
                        }

                    } else {
                        if(dish.level==="hard"){
                            return product
                        }
                    }
                }
            }
          
          }
     

        return (  <Page>
                    <Title> Proyects </Title>
                    <hr/>
                    <Level>    
                        {buttons.easy}
                        {buttons.medium}
                        {buttons.hard} 
                        {buttons.all}
                    </Level>
                    <DivMostrador>
                        <Mostrador className={mostrador}>{mostrador}</Mostrador>
                    </DivMostrador>
                    <Filtro>
                        { products.map(renderProducts) }
                    </Filtro>

            </Page>

         );

}
export default Filter;


const Page = styled.div`
background-color: #fff1df;
`;
const Title = styled.h1`
padding: 40px 0 30px 0;
text-shadow: 1.5px 1.5px #000;
font-size: 50px;
text-align: center;
color: #1b93c2;
font-family: "Press Start 2P";
`;
const Level = styled.div`
padding: 0px 0 0px 0;
margin-top: -10px;
width: 100%;
box-sizing: border-box;
`;
const Button = styled.div`
width: 33.33%;
height: auto;
padding: 20px 0 20px 0;
display: inline-block;
margin-left: auto;
margin-right: auto;
`;
const All = styled.div`
margin-top: -10px;
padding: 0px 27px 20px 0;
width:100px
float: right;
height:50px;
`;
const DivMostrador = styled.div`
width:100%;
height:auto;
text-align: center;
.Easy{
    background-color: #908fbb;
    border-bottom:solid 7px #66658d;
}
.Medium{
    background-color: #4ea6a6;
    border-bottom:solid 7px #206f6f;
}
.Hard{
    background-color: #da6964;
    border-bottom:solid 7px #ad4b47;
}
.All{
    background-color: #ffc500;
    text-shadow: 2px -1px #66658d;
    color: #fff;
    border-bottom:solid 7px #b98f00;
}
.Free{
    background-color: #50a64e;
    border-bottom:solid 7px #206f33;
}
.Paid{
    background-color: #0097d3;
    border-bottom:solid 7px #002e83;
}
@media (min-width: 768px) {  
}
`;
 const Mostrador = styled.h2`
    font-family: "Press Start 2P";
    color:#eaeaea;
    text-shadow: 5px -1px #000;
    font-size: 20px;
    letter-spacing: 3px;
    padding: 15px 0 13px 0 ;
    background-color: #e9b962;
    .free{
        color:#00CC29;
    }
    `;
const Filtro = styled.div`
    width: auto;
    height: auto;
    margin: 15px 0 0 0;
    text-align: center;
    padding: 10px 0px 0px 0px;
`;

